<template lang="pug">
Modal(v-model="visible" title="" @on-visible-change="onChange" @on-ok="setTime")
  .lineCard
    .title
      p  
    .setting
      RadioGroup(v-model="remindData.isNow")
        Radio(label="1")
          span 
        Radio(label="0")
          span 
    .datePicker
      DatePicker(v-if="remindData.isNow === '0'" @on-change="datachange" type="datetime" :options="todayAfter" format="yyyy-MM-dd HH:mm" placeholder="")
</template>
<script>
import { remind } from "@/apis/course.js"
export default {
  name: "LineRemind",
  props: {
    value: {
      type: Boolean,
      default: () => false
    },
    secIndex: {
      type: Number,
      default: () => -1
    }
  },
  watch: {
    value(val) {
      this.visible = val;
    },
    secIndex(val) {
      this.sectionIndex = val;
    }
  },
  data() {
    return {
      visible: this.value,
      sectionIndex: this.secIndex,
      classId: this.$route.params.classID,
      remindData: {
        time: "0",
        isNow: "0"
      },
      todayAfter: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000;
        }
      },

    }
  },
  methods: {
    onChange(val){
      this.visible = val;
      this.$emit("input", val);
    },
    datachange(date){
      let timestamp = Date.parse(date);
      this.remindData.time = timestamp/1000;
    },
    async setTime(){
      let index = this.sectionIndex + 1;
      if(this.remindData.isNow == "1"){
        this.remindData.time = 0;
      }
      let params = {
        time: parseInt(this.remindData.time),
        isNow: parseInt(this.remindData.isNow)
      }
      let res = await remind(this.classId, index, params);
      if(res.data.status.code === 0){
        this.$Message.success("");
        this.onChange(false);
      }else if(res.data.status.code === 9489){
        this.$Message.error("");
      }else{
        this.$Message.error("" + res.data.status.code);
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.lineCard{
  min-height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .title{
    flex: 1;
    align-items: center;
  }
  .setting{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  .datePicker{
    flex: 2;
  }
}
</style>