<template lang="pug">
Card.cardInfo
	.delBtn
		Poptip.poptext(confirm title="" ok-text="" cancel-text="" @on-ok="delClass" @on-cancel="")
			Icon(type="ios-close" size="30")
	.imgframe
		img(v-if='this.classDetail.imgUrl != "" ' :src="this.classDetail.imgUrl" )
		img(v-else :src="defaultClass") 
	.detail
		.title {{ this.classDetail.topic }}
		p {{ this.classDetail.sectionNum }} 
		p  {{ this.classDetail.teacherName }}
		p  {{ this.classDetail.isPublic == 0 ? "" : "" }}
		p  {{ this.classDetail.isOpen == 0 ? "" : "" }}
		p(style="text-align: left") 
		p.intro {{ this.classDetail.intro }}
	.enter
		Button.btn(type="error" @click="editClass") 
		Button.btn(type="info" @click="enterClass") 
</template>
<style lang="scss" scoped>
.cardInfo{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 300px;
	height: 470px;
	flex-wrap: nowrap;
	margin: 5px 10px;
	.delBtn{
		text-align: right;
		flex: 1;
	}
	.imgframe{
		flex: 1;
		height: 110px;
		line-height: 110px;
		img {
			max-height: 100%;
			vertical-align: middle;
		}
	}
	.detail{
		flex: 1;
		text-align: left;
		.title{
			font-size: 22px;
			font-weight: bold;
			margin: 5px auto;
			text-align: center;
		}
		.intro{
			flex: 1;
			width: 90%;
			height: 110px;
			margin: auto;
			padding: 5px;
			display: -webkit-box;
			overflow: hidden;
			-webkit-line-clamp: 5; //
			-webkit-box-orient: vertical;
			white-space: normal;
		}
	}
	.enter{
		flex: 1;
		.btn{
			margin: 10px 5px;
		}
	}
}
</style>
<script>
import defaultClass from "@/assets/defaultClass.png";
export default {
  name: `ClassCard`,
  data() {
    return {
      defaultClass
    };
  },
  props: {
    classDetail: {
      type: Object,
      default: null
    }
  },
  methods: {
    editClass() {
      this.$emit("editClass", this.classDetail);
    },
    enterClass() {
      this.$emit("enterClass");
    },
    delClass() {
			if (this.classDetail.isOpen === 1){
				this.$Message.error("");
				return;
			}
      this.$emit("deleClass", this.classDetail.classId);
    }
  }
};
</script>
