<template lang="pug">
#SignUp.signup
  Card.signinCard
    h1 
    Form(:ref="signUp")
      FormItem(label="Account", prop="account")
        Input(type="email", placeholder="", v-model="signUp.account")
      FormItem(label="Password", prop="password")
        Input(type="password", placeholder="", v-model="signUp.password")
      FormItem(label="", prop="name")
        Input(type="text", placeholder="", v-model="signUp.name")
      FormItem(label="  ()", prop="department")
        Input(type="text", v-model="signUp.department")
      FormItem
        Button(type="primary", @click="register()") 
        Button(type="primary", @click.native="toSingIn()") 
</template>

<style lang='scss' scoped>
.signup {
  display: flex;
  align-items: center;
  justify-content: center;
}
.signinCard {
  width: 600px;
  button {
    margin: 10px;
  }
}
</style>

<script>
import { signUp } from "@/apis/course.js";
export default {
  name: "SignUp",
  data() {
    return {
      signUp: {
        account: "",
        password: "",
        name: "",
				department: ""
			}
    };
  },
  methods: {
    toSingIn() {
      this.$router.push({
        path: "signin"
      });
    },
    register() {
      signUp({
        account: this.signUp.account,
        password: this.signUp.password,
        department: this.signUp.department,
        name: this.signUp.name
      }).then(req => {
				if (req.data.status.code === 0) {
					this.$Message.success("");
					this.toSingIn();
				}
      });
    }
  }
};
</script>
